import React, { useState, useReducer, useEffect, useContext, useCallback, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { Player, Result } from '../../../interfaces';
import styles from '../css/sidebar.module.css';
import { FaThumbtack, FaSearch, FaFilter, FaAngleDown, FaEye, FaBullhorn, FaAngleUp, FaAngleRight } from 'react-icons/fa';
import Dropdown from '../../../components/Dropdown';
import AutoComplete from '../../../components/AutoComplete';
import { LoginContext } from '../../../Contexts';
import { Match } from '../../../models';
import Pie from '../PieChart';
import ReactMarkdown from 'react-markdown';
import { parseHtml, RouterLink } from '../../../utils/plugins';
import { truncate } from '../../../utils/prototype';

export default function useTable({
	title,
	getName
}: {
	title?: string
	getName: (opp: Player) => string
}) {

	const [searched, setSearched] = useState([] as Player[]);
	const [visible, setVisible] = useState('all');

	/**
	 * Manage table state as to which rows are expanded
	 */
    const [expanded, setExpanded] = useReducer((state: string[], action: string[] | string) => {
		if (Array.isArray(action)) return action;
		let list = state.slice(0) || [];
		if (state.includes(action)) list.splice(list.indexOf(action), 1);
		else list.push(action);
		return list;
	}, [decodeURIComponent(window.location.hash.slice(1).split('.').slice(0, 2).join('.'))]);


	const [scrolled, setScrolled] = useState(false);
	/* eslint-disable-next-line react-hooks/exhaustive-deps */
	useEffect(() => {
		if (scrolled) return;
		if (!window.location.hash) return;
		let id = window.location.hash.slice(1);
		let elem = document.getElementById(id);
		if (elem) {
			elem.scrollIntoView({
				behavior: 'auto'
			});
			setScrolled(true);
		}
	});

	const Sidebar = useCallback(function Sidebar({ id, showSidebar, playerDict, fetchGames, matches, round, matchDict, results, announcements }: {
		id: string
		playerDict: Map<string, Player>
		fetchGames: (ids?: string[]) => Promise<void>
		matches: Match[],
		round: number,
		matchDict: {[key: string]: Match}
		results: Result[]
		showSidebar: boolean
		announcements: {
			announcement: string
			createdAt: string
			updatedAt: string
		}[]
	}) {

		const { isLoggedIn } = useContext(LoginContext);

		const last = announcements[announcements.length - 1];
		const pie = useMemo(() => <Pie
			matches={matchDict}
			round={round}
			total={results.length}
			donutWidth={30}
			height={200}
		/>, [matchDict, round, results.length]);

		return (
			<div className={[
				styles.sidebar,
				showSidebar ? styles.animate : ''
			].join(' ')}>
				<div className={styles.name}>
					{title}
				</div>
				{last ?
					<div className={styles.announcement}>
						{true ? //TODO
							<Link to={['', 't', id, 'announcements'].join('/') + '#latest'} className={styles.title}><FaBullhorn />Latest News</Link> :
							<div className={styles.title}><FaThumbtack style={{ transform: 'rotate(45deg)' }} />Pinned announcement</div>
						}
						<div className={styles.body}>
							{last ? <ReactMarkdown
								source={truncate(last.announcement.trim().split('*').join('').split('\n').map(l => l.trim()).join(' '), 200)}
								escapeHtml={false}
								astPlugins={[parseHtml]}
								renderers={{
									link: RouterLink
								}}
							/> : null}
						</div>
						<div className={styles.dates}>
							{new Date(last.createdAt).toString().slice(0, 21)}
						</div>
						<Link to={['', 't', id, 'announcements'].join('/')} className={styles.more}>
							View all<FaAngleRight />
						</Link>
					</div> : null
				}
				{pie}
				<div className={styles.searchBar}>
					<FaSearch />
					<AutoComplete
						onChange={(options: Player[]) => setSearched(options)}
						onSubmit={() => Promise.resolve()}
						action=''
						options={Array.from(playerDict.values())}
						convert={(p: Player) => getName(p)}
						styles={styles}
						placeholder={'Search'}
					/>
				</div>
				<div className={['button', styles.expandAll].join(' ')} onClick={() => {
					if (matches.length === expanded.length) setExpanded([]);
					fetchGames().then(() => setExpanded(matches.map(m => m.id)))
				}}>
					<FaEye />{matches.length === expanded.length ? 'Hide' : 'View'} all scores
				</div>
				{!isLoggedIn ? null : <div className={['button', styles.jumpTo].join(' ')} onClick={() => document.getElementsByClassName('highlighted')[0].scrollIntoView()}>
					<FaAngleDown />Find my pairing
				</div>}
				<Dropdown
					current={visible}
					options={[
						'all',
						'aborted',
						'pending',
						'scheduled',
						'registered',
						'live',
						'finished',
						'confirmed'
					].filter(s => s === 'all' || matches.some(m => m && m.round === round && m.status === s))}
					resolve={setVisible}
					className={styles.statusPicker}
					headerIcon={<FaFilter />}
				/>
				<div className={['jumpToTop'].join(' ')} onClick={() => window.scrollTo(0, 0)}>
					<FaAngleUp />
				</div> 
			</div>
		)
	}, [getName, expanded.length, visible, setSearched, setVisible, title]);
	(Sidebar as any).whyDidYouRender = true;

	return {
		searched, expanded, visible, setExpanded, Sidebar
	};
	
}