import React, { useState, useCallback, useEffect, useReducer, useRef, RefObject } from 'react';
import styles from './css/teams.module.css';
import { GET } from '../utils/requests';
import { Team as TeamInterface, ProfileData } from '../interfaces';
import Info from '../user/Info';
import { FaTh, FaColumns } from 'react-icons/fa';
import PlayerLine from '../components/PlayerLine';
import { getDocumentHeight } from '../utils/prototype';

let offsetY = 0;

export default function Teams() {

	const [mode, setMode] = useState('compact' as 'compact' | 'grid');
	const [preview, setPreview] = useReducer((state: null | TeamInterface & ProfileData, action: null | TeamInterface & ProfileData) => {
		if (state?.id === action?.id) return null;
		return action;
	}, null as null | TeamInterface & ProfileData)
	const [teams, setTeams] = useState([] as (TeamInterface & ProfileData)[]);
	const updateTeams = useCallback(() => GET({ url: '/teams' })
		.then(setTeams), [setTeams]);
	useEffect(() => {
		updateTeams();
	}, [updateTeams]);

	const prev = useRef() as RefObject<HTMLDivElement>
	const [scrolled, setScrolled] = useState(false);
	const [top, setTop] = useReducer((state: number, action: { value?: number, delta?: number }) => {
		if (typeof action.value === 'number') return action.value;
		if (!action.delta) return state;
		return state - action.delta;
	}, window.innerHeight * 18 / 100);
	const updateScrolled = useCallback((e: Event) => {
		if (window.scrollY) setScrolled(true);
		else setScrolled(false);
		if (!prev.current) return;
		let scrollTop = (e.srcElement as Document).scrollingElement?.scrollTop ?? 0;
		let deltaY = scrollTop - offsetY;
		offsetY = scrollTop;
		if (deltaY < 0) {
			if (
				scrollTop > window.innerHeight * 18 / 100 &&
				!(window.innerHeight + window.scrollY + 300 > getDocumentHeight())
			) return;
		} else {
			if (
				prev.current.offsetTop + prev.current.offsetHeight < window.innerHeight &&
				!(window.innerHeight + window.scrollY + 300 > getDocumentHeight())
			) return;
			if (window.innerHeight + window.scrollY + 50 > getDocumentHeight()) return;
		}
		setTop({ delta: deltaY })
	}, [setScrolled, prev, setTop]);
	useEffect(() => {
		window.addEventListener('scroll', updateScrolled);
		return () => window.removeEventListener('scroll', updateScrolled);
	}, [updateScrolled]);

	return (
        <div id='main'>
			<section className={styles.container}>
				<div className={styles.title}>
					<div><b>Teams</b></div>
					<div className={'light'}>{teams.length}</div>
					<div className={styles.menu}>
						<label>Display</label>
						<FaColumns onClick={() => setMode('compact')} className={mode === 'compact' ? styles.inactive : styles.active} />
						<FaTh onClick={() => setMode('grid')} className={mode === 'grid' ? styles.inactive : styles.active} />							
					</div>
				</div>
				{mode === 'compact' ?
					<>
						<div className={styles.compact}>
							{teams.sort((a, b) => a.name.toLowerCase().charCodeAt(0) - b.name.toLowerCase().charCodeAt(0)).map((u, i) => {
								return <PlayerLine key={['team', i].join('.')} profile={u} i={i} type='team' onClick={() => setPreview(u)} />
							})}
						</div>
						<div style={{ top }} className={[styles.preview, scrolled ? styles.scrolled : ''].join(' ')} ref={prev}>
							{preview ?
								<Info id={preview.id} profile={preview} type='team' small={false} showLinks /> :
								null
							}
						</div>
					</> :
					<div className={styles.userTable}>
						{teams.sort((a, b) => a.name.toLowerCase().charCodeAt(0) - b.name.toLowerCase().charCodeAt(0)).map((u, i) => {
							return <Info key={['team', i].join('.')} id={u.id} profile={u} type='team' i={i} />
						})}
					</div>
				}
			</section>
		</div>
	)
}